import { Component, EventEmitter, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { PettyCashService } from '../../../services/petty-cash.service';
import { TableColumn, TableConfig } from '../../../../../../../shared/components/table-advanced/table-advanced-interfaces';
import { SharedService } from '../../../../../../../services/shared.service';
import { AddExpenseComponent } from '../../../add-expense/add-expense.component';
import { ExpenseDetailsModalComponent } from './expense-details-modal/expense-details-modal.component';

@Component({
  selector: 'app-pending-expenses',
  templateUrl: './pending-expenses.component.html',
  styleUrls: ['./pending-expenses.component.scss']
})
export class PendingExpensesComponent {
  expensesList = [];
  pageNo = 1;
  total = 0;
  limit = 6;
  tableConfig: TableConfig = {
    paging: false,
    multiSelect: true,
    filter: {
      Sort: 1,
      PageSize: this.limit,
    },
  };
  tableColumns: TableColumn[] = [];
  @Output() eventData = new EventEmitter();
  constructor(
    private _pettyCashService: PettyCashService,
    private _modalService: NgbModal,
    private sharedService: SharedService,
  ) { }
  ngOnInit() {
    this.initTableColumns();
    this.getPendingExpense();
  }
  openModalExpenseView(item) {
    const modalRef = this._modalService.open(ExpenseDetailsModalComponent, {
      size: 'lg',
    });
    modalRef.componentInstance.expenseDetails = { ...item };
  }
  onMultiSelected(event) {
    //console.log(event);
    let selectedItems = event.selectedItems;
    if (selectedItems?.length > 0) {
      let expenseIds = selectedItems.map(x => x.expenseRecordId);
      let total = selectedItems.reduce((acc, cur) => {
        acc += cur?.expenseTotalAmount
        return acc;
      }, 0);
      this.eventData.emit({ totalAmount: total.toFixed(3), expenseIds });
    }
    else
      this.eventData.emit({ totalAmount: 0 });
  }
  getPendingExpense() {
    let params = `?status=2003&sort=${this.tableConfig.filter.Sort}&pageSize=10000&registerId=${this.sharedService.getRegister?.registerObj?.id}`;
    this._pettyCashService.getPettyCashExpensesNew(params).subscribe((response: any) => {
      this.expensesList = response.data;
      this.total = response?.info?.totalRecordCount;
    })
  }

  onSortChange(sort: any) {
    if (sort?.direction && sort?.column) {
      switch (sort.column) {
        case 'invoiceNo':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 3 : 2;
          break;
        case 'category[0].lookupName':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 5 : 4;
          break;
        case 'createdAt':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 7 : 6;
          break;
        case 'createdBy.fullName':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 9 : 8;
          break;
        case 'expenseTotalAmount':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 11 : 10;
          break;
        case 'status':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 13 : 12;
          break;
        default:
          break;
      }
    } else {
      this.tableConfig.filter.Sort = 1;
    }
    this.getPendingExpense();
  }
  initTableColumns() {
    this.tableColumns = [
      { key: 'invoiceNo', label: 'Invoice No', canSort: true, },
      { key: 'invoiceDate', label: 'Invoice Date', dateFormat: 'mediumDate' },
      { key: 'category[0].lookupName', label: 'Category', canSort: true, },
      { key: 'createdAt', label: 'Date', canSort: true, dateFormat: 'mediumDate', placeholder: '---' },
      { key: 'createdBy.fullName', label: 'Employee', canSort: true, },
      { key: 'expenseTotalAmount', label: 'Total Amount', canSort: true, currency: { decimalFormat: '2.3-3', appendText: ' JOD' } },
      { key: 'status', label: 'Status', canSort: true, },
      { key: 'action', label: 'Action', },
    ];
  }
}
